*,*::before,*::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body{
  background: url("./images/bergwetter.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.container{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  
  
}

.left-side{
  height: 70vh;
  width: 25%;
  background: url('./inner.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.right-side {
  background-color: rgba(0, 0, 0, 0.7);
  width: 18%;
  height: 70vh;
}

.span-place{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  color: white;
  font-size: 1.5rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.div-place{
  padding: 1.5rem;
}

.time-temp{
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  margin-bottom: .75rem;
}

.date {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.span-date{
  font-size: 2rem;
}

.temp{
  font-size: 3.5rem;
}

.right-side{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mb-icon{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 1rem;
  margin-top: 1rem;
  font-size: 2.5rem;
  border-bottom: 1px solid grey;
}

.weather-type{
  color: white;
}

.weather-city{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

input{
  outline: none;
  background-color: transparent;
  border: none;
  font-size: 1rem;
  color: white;
  border-bottom: 1px solid grey;
}

button{
  background-image: url('./images/search.png');
  color: white;
  height: 50px;
  width: 50px;
  background-size: cover;
  margin-top: 10px;
  border-radius: 45%;
  border: none;
}

.span-row{
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: .45rem;
  font-size: .9rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border-top: 1px solid grey;
  font-weight: 500;
}